










































import Vue from 'vue';
import Body from '@/models/Body';

export default Vue.extend({
  name: 'BodyDialog',
  data: () => ({
    showDialog: false,
    body: <Body>{}
  }),
  methods: {
    toggleDialog() {
      this.showDialog = !this.showDialog;
    },
    saveBody() {
      this.$emit('saveBody', this.body);
      this.body = <Body>{};
      this.toggleDialog();
    }
  }
});
